<template lang="html">
    <div class="fleet">
    	
    	<div class="fleet-dog">
    		<div class="waybill-header">
			<div class="header-zuo">
				<h2>车辆分享/</h2>
				<p>啦啦啦啦啦啦啦啦啦</p>
			</div>
			<p><a  onclick="window.history.go(-1)">返回上一页</a></p>
		</div>
    		<div class="fleet-button">
         <el-table
            ref="multipleTable"
            :data="tableData3"
            border
            tooltip-effect="dark"
            style="width: 100%"
            @selection-change="handleSelectionChange">
             <el-table-column
            type="selection"
            width="55">
            </el-table-column> 
            <el-table-column
            label="车辆信息"
            width="">
            <template scope="scope">{{ scope.row.msg}}</template>
            </el-table-column>
            <el-table-column
            prop="name"
            label="司机信息"
            width="130">
            <template scope="scope">
              <span>{{scope.row.name}}</span>
              <p>{{scope.row.phone}}</p>
            </template>
            </el-table-column>
            <el-table-column
            prop="address"
            label="定位信息"
            show-overflow-tooltip>
            <template scope="scope">
                <span>{{scope.row.address}}</span> 
               <p>{{scope.row.date}}</p>  
            </template>
            </el-table-column>
           
        </el-table>
         <el-autocomplete
      class="inline-input"
      v-model="state1"
      :fetch-suggestions="querySearch"
      placeholder="请输入要分享的企业代码或公司名称"
      @select="handleSelect"
    ></el-autocomplete>
   <router-link  tag="el-button"  type="success" :to="{path:'/shared'}">确定分享</router-link>
        </div>
        </div>
    </div>
</template>


<script>
export default {
    data () {
      return {
        radio: '1',
        tableData3: [{
          msg: '沪DG5060 厢式车 9.6米 10吨 30方',
          name: '衣龙魁',
          phone:'15064558893',
          address: '山东省烟台市莱阳市G204(龙门西路)',
          date:'2016-11-17 18:52:10'
        },{
          msg: '沪DG5060 厢式车 9.6米 10吨 30方',
          name: '衣龙魁',
          phone:'15064558893',
          address: '山东省烟台市莱阳市G204(龙门西路)',
          date:'2016-11-17 18:52:10'
        },{
          msg: '沪DG5060 厢式车 9.6米 10吨 30方',
          name: '衣龙魁',
          phone:'15064558893',
          address: '山东省烟台市莱阳市G204(龙门西路)',
          date:'2016-11-17 18:52:10'
        }],
        multipleSelection: [],
         restaurants: [],
        state1: ''

      };
    },
    methods:{
        handleSelectionChange(val) {
            this.multipleSelection = val;
        },
        querySearch(queryString, cb) {
        var restaurants = this.restaurants;
        var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
        // 调用 callback 返回建议列表的数据
        cb(results);
      },
      createFilter(queryString) {
        return (restaurant) => {
          return (restaurant.value.indexOf(queryString.toLowerCase()) === 0);
        };
      },
      loadAll() {
        return [
          { "value": "三全鲜食（北新泾店）", "address": "长宁区新渔路144号" },
          { "value": "Hot honey 首尔炸鸡（仙霞路）", "address": "上海市长宁区淞虹路661号" },
          { "value": "新旺角茶餐厅", "address": "上海市普陀区真北路988号创邑金沙谷6号楼113" },
          { "value": "泷千家(天山西路店)", "address": "天山西路438号" },
          { "value": "胖仙女纸杯蛋糕（上海凌空店）", "address": "上海市长宁区金钟路968号1幢18号楼一层商铺18-101" },
          { "value": "贡茶", "address": "上海市长宁区金钟路633号" },
        
        ];
      },
      handleSelect(item) {
        console.log(item);
      }
    },
    mounted() {
      this.restaurants = this.loadAll();
    }
}
</script>


<style lang="less" scoped>
.waybill-header{
			height: 90px;
			width:800px;
			display: flex;
			justify-content: space-between;
			line-height: 90px;
			/*border:1px solid #ccc;*/
			
		}
		.waybill-header .header-zuo{
				display: flex;
				
			}
			.waybill-header .header-zuo h2{
					color: #4046b6;
					margin-right: 30px;
				}
			.waybill-header p{
				color: #ccc;
				font-size: 13px;
			}
.fleet-dog{
		background: #FAFBFF;
		padding: 20px 30px;
		 height:960px;
		
}
.fleet-dog h2{
	line-height: 70px;
	color:#355294 ;
}
.fleet-dog h2 span{
	color:#8B92A5;
	font-size:15px;
}
.fleet-button{
	height: 800px;
	padding: 20px;
	background: white;
	border: 1px solid darkgray;
}
    table{
        font-size: 12px;
    }
    .el-table th>.cell {
      color: #8290B3;
    }
    .el-table__row{
      height: 100px;
        p{
          color:#989BAE;
        }
    }
    .color{
    
        color: #4663A9;
        border-right: 1px solid #D7D7D9;
        padding-right: 4px;
    }
    .cololr2{
        color:#B4B4B4;
    }
    .inline-input{
    	
    	margin-top: 20px;
    	width: 350px;
    	
    }
</style>


